<template>
  <div id="app">
    <div class="question-container">
      <div class="swiper">
         <van-swipe :autoplay="3000">
          <van-swipe-item v-for="(image, index) in images" :key="index" @change="onChange(index)">
            <img :src="image"/>
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="questions-list clearfix">
        <div class="questions radius10" v-for="(item, index) in questions"  :key="index" >
          <router-link :to="{name:'detail',query:{id: item.id}}">
            <div class="questions-pic">
              <img :src="item.cover" :onerror="errorUserPhoto">
            </div> 
            <div class="questions-detail">
              <p>前端前端前端前端前端前端前端前端前端前端前端</p>
              <p>
                <span v-for="(item, index) in item.tags"  :key="index" >{{item.tag_name}}</span>
              </p>
            </div> 
          </router-link>
          
        </div>
      </div>
      
      <van-pagination 
        v-model="currentPage" 
        :total-items="6" 
        :show-page-size="3" 
        force-ellipses
      />
    </div>
    <router-view/>
  </div>
</template>

<script>
import api from '../assets/importantJs/api.js'
import userPhoto from '@/assets/image/errorImg.gif'

export default {
  name: 'app',
  components: {
    // HelloWorld
  },
  data(){
    return {
      currentIndex: 0,
      show: false,
      currentPage: 1,
      images: [
        require('./../assets/image/timg.jpg'),
        require('./../assets/image/timg1.jpg'),
        require('./../assets/image/timg2.jpg')
      ],
      questions: [],
      errorUserPhoto:'this.src="' + userPhoto + '"',
    }
  },
  methods: {
    onChange(index) {
      console.log(index)
      this.currentIndex = index;
    },
    getIndexList(){
      let t = this;
      api.getIndexList({
        page: 1
      }).then((res)=>{
        if(res && res.data && res.data.data && res.data.data.data.length){
          let result = res.data.data.data;
          t.questions = result
        }
      })
    }
    // 获取首页列表数据
    
  },
  mounted(){
    let t = this;
    t.getIndexList();
  },
}
</script>