<template>
  <div id="app">
    <div class="header flex-between">
      <span @click="(show = !show)"><i class="iconfont icon-daohang"></i></span>
      <span>22222222222</span>
      <span><i class="iconfont icon-sousuo"></i></span>
    </div>
    <div>
      <router-link :to="{name:'markdown'}">编辑
      </router-link>
    </div>
    <router-view/>
    <div class="footer">
      <div class="title">
        <div class="title-m">
          <span>受欢迎的文章</span>
        </div>
        <div  v-for="(item, index) in article"  :key="index" >{{item.tag_name}}</div>
      </div>
      <div class="title">
        <div class="title-m">
          <span>主题</span>
        </div>
        <p  v-for="(item, index) in relate"  :key="index" >{{item.category_name}}</p>
      </div>
    </div>
    <div class="footer-copy">
    </div>
    <div class="nav" v-if="show">
      <div class="close" @click="(show = !show)"><i class="iconfont icon-chenghao"></i></div>
      <div class="title-l">首页</div>
      <van-collapse v-model="activeNames">
        <van-collapse-item title="标题1" name="1">
          <div  class="title">
          <p  v-for="(item, index) in relate"  :key="index" >{{item.category_name}}</p>
          </div>
        </van-collapse-item>
      </van-collapse>
      <div class="title-l">关于我</div>
    </div> 
    <!-- 返回顶部 -->
    <transition name="fade">
      <div class="to-top" v-if="toTop" @click="goTop">
        <i class="iconfont icon-jiantou-copy"></i>
      </div>
    </transition>
  </div>
</template>

<script>
import api from './assets/importantJs/api.js'

export default {
  name: 'app',
  components: {
    // HelloWorld
  },
  data(){
    return {
      show: false,
      activeNames: [''],
      toTop: false,
      relate: [],
      article: []
    }
  },
  methods: {
    windowScroll () {
      //滚动条距离页面顶部的距离
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop //原生兼容
        if(scrollTop > 100){
          this.toTop = true;
        }else{
          this.toTop = false;
        }
    },
    goTop(){
      let t = this
      t.timer = setInterval(function () {
        let osTop = document.documentElement.scrollTop || document.body.scrollTop
        let ispeed = Math.floor(-osTop / 5)
        document.documentElement.scrollTop = document.body.scrollTop = osTop + ispeed
        t.isTop = true
        if(osTop > 100){
          this.toTop = false;
        }
        if (osTop === 0) {
          clearInterval(t.timer)
        }
      }, 30)
    },
    getRelate(){
      let t = this;
      api.getRelate({}).then((res)=>{
        console.log(res)
        if(res && res.data && res.data.data.length){
          let result = res.data.data;
          t.relate = result
        }
      })
    },
    getArticle(){
      let t = this;
      api.getArticle({}).then((res)=>{
        console.log(res)
        if(res && res.data && res.data.data.length){
          let result = res.data.data;
          t.article = result
        }
      })
    }
  },
  mounted () {
    let t = this;
    window.addEventListener('scroll', this.windowScroll)
    t.getRelate()
    t.getArticle()
  },
  destroyed () {
    window.removeEventListener('scroll', this.windowScroll)
  }
}
</script>