<template>
  <div>
    <input type="text" placeholder="" v-model="title"/>
    <mavon-editor v-model="context" :toolbars="toolbars" />
  </div>
  
</template>
<script>
import api from '../assets/importantJs/api.js'
export default {
  name: 'markdown',
  data(){
    return {
      title: '',
      tags: '',
      category_id: '',
      context: ' ',//输入的数据
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        mark: true, // 标记
        superscript: true, // 上角标
        quote: true, // 引用
        ol: true, // 有序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        help: true, // 帮助
        code: true, // code
        subfield: true, // 是否需要分栏
        fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        /* 1.3.5 */
        undo: true, // 上一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true // 导航目录
      }
    }
  },
  methods: {
    addArticle() {
      let t = this;
      api.addArticle({
        title: t.title,
        tags: t.tags,
        category_id: t.category_id,
        context: t.context,
      }).then((res)=>{
        if(res && res.code == 200){
          console.log('添加成功') 
        }
      })
    }
  },
}
</script>
