<template>
  <div class="item">
    <div class="item-title">
      <p>{{context.title}} </p>
      <p>{{context.created_at}}</p>  
    </div>
    <mavon-editor
      class="md"
     :value= "context.content"
     :subfield = "prop.subfield"
     :defaultOpen = "prop.defaultOpen"
     :toolbarsFlag = "prop.toolbarsFlag"
     :editable= "prop.editable"
     :scrollStyle= "prop.scrollStyle"
  ></mavon-editor>
  </div>
  
</template>

<script>
import apis from '../assets/importantJs/api.js'
export default {
  name: 'detail',
  props: {
    msg: String
  },
  data(){
    return {
      context: '',
      articleDetail: 'aaaaaaaaaaaaaaaaaaaaaaaaa',
      prop: {
        subfield: false,// 单双栏模式
        defaultOpen: 'preview',//edit： 默认展示编辑区域 ， preview： 默认展示预览区域 
        editable: false,
        toolbarsFlag: false,
        scrollStyle: true
      }
    }
  },
  methods: {
    getDetails(){
      let t = this;
      let id = t.$route.query.id;
      apis.getDetail({
        id: id
      }).then((res)=>{
        console.log(res)
        if(res && res.data && res.data && res.data.data){
          let result = res.data.data;
          t.context = result
        }
      })
    }
  },
  mounted() {
    let t = this;
    t.getDetails();
  },
}
</script>

