import Vue from 'vue'
import Router from 'vue-router'
 
//组件模块
import Detail from '../components/detail'
import Index from '../components/index'
import Mark from '../components/markdown'
Vue.use(Router)
 
export default new Router({
  routes: [
    { 
      path: '/',  
      name: 'index', 
      component: Index
    },
    { 
      path: '/detail',  
      name: 'detail', 
      component: Detail
    },
    { 
      path: '/markdown',  
      name: 'markdown', 
      component: Mark
    }
  ]
})